.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


a {
  color: #61dafb;
  cursor: cell;
  text-decoration: none;
}

.Button {
  background-color: #282c34;
  justify-content: center;
  display: flex;
  padding: 20px;
  margin: 20px;
  border-radius: 20px;
  border: 4px dashed #61dafb;
  width: 20rem;
  font-size: 15px;
}

@media only screen and (max-width: 500px){
  .App {
    padding: 15px;
  }

  .Button {
    align-items: stretch;
    width: 15rem;
  }
}